import React, {Dispatch, SetStateAction, useState} from 'react'
import {RiArrowDownSLine, RiSubtractLine} from 'react-icons/ri'
import {Button, Tag} from 'element-react'

interface CollapseItemProps {
    uuid: number,
    label: string,
    active: boolean,
    setActiveIndex: Dispatch<SetStateAction<number>>
}

function CollapseItem(props: CollapseItemProps) {
    const {
        uuid,
        label,
        active,
        setActiveIndex
    } = props

    const onClick = () => {
        setActiveIndex(active ? -1 : uuid)
    }

    return (
        <div>
            <div className="flex justify-between items-center py-2 cursor-pointer border-b-2 border-solid"
                 onClick={onClick}>
                <h2>{label}</h2>
                <div className="text-2xl">
                    {
                        active ? <RiSubtractLine/> : <RiArrowDownSLine/>
                    }
                </div>
            </div>
            <div className={`h-0 ${active && 'h-auto'} overflow-y-hidden`}>
                <div className="p-4">
                    <Tag type="primary">Tag1</Tag>
                </div>
            </div>
        </div>
    )
}

interface CollapseProps {
    height?: string,
}

function Collapse(props: CollapseProps) {
    const {height = '300px'} = props
    const [activeIndex, setActiveIndex] = useState(-1)

    return (
        <div className="flex flex-col justify-between" style={{height}}>
            <div>
                <h2 className="text-2xl font-medium pb-2">Advanced Filter</h2>
                <CollapseItem
                    label="Brand"
                    uuid={0}
                    active={activeIndex === 0}
                    setActiveIndex={setActiveIndex}
                />
                <CollapseItem
                    label="Resolution & Frame Rate"
                    uuid={1}
                    active={activeIndex === 1}
                    setActiveIndex={setActiveIndex}
                />
                <CollapseItem
                    label="Function"
                    uuid={2}
                    active={activeIndex === 2}
                    setActiveIndex={setActiveIndex}
                />
                <CollapseItem
                    label="Certification"
                    uuid={3}
                    active={activeIndex === 3}
                    setActiveIndex={setActiveIndex}
                />
                <CollapseItem
                    label="Price"
                    uuid={5}
                    active={activeIndex === 5}
                    setActiveIndex={setActiveIndex}
                />
                <div className="py-4 flex justify-end">
                    <Button type="primary">Search</Button>
                </div>
            </div>

            <div className="border border-2 p-4">
                {
                    new Array(15).fill(1).map(() =>
                        <Tag
                            className="mr-2"
                            type="primary"
                            closable={true}
                        >Tag1</Tag>
                    )
                }
            </div>
        </div>
    )
}

export default Collapse
