import React, {useState} from 'react'
import ProductCard, {ProductCardProps} from "../components/ProductCard";
import {Pagination} from "element-react";
import ProductDialog from "./ProductDialog/index";

function initProductCardData(): ProductCardProps {
    return {
        name: 'NexiGo',
        price: 54.99,
        code: 'A229A/N930AF',
        resolution: '1080P',
        feature: 'HDR, Wide Angle, Adjustable FOV',
        certification: 'Zoom'
    }
}

function ProductCards() {
    const [dialogVisible, setDialogVisible] = useState(false)
    const productCardData = initProductCardData()

    const openDialogHandler = () => {
        setDialogVisible(true)
    };

    return (
        <div>
            <div className="flex flex-wrap -mx-1 mt-4">
                {
                    new Array(10).fill(1).map((_, index) =>
                        <div className="mx-1 mb-2" style={{width: 'calc(20% - 0.5rem)'}} onClick={openDialogHandler}>
                            <ProductCard
                                {...productCardData}
                                order={index + 1}
                                tagContext={index === 0 ? 'Best Seller' : ''}
                            />
                        </div>
                    )
                }
            </div>

            <div className="flex justify-center my-8">
                <Pagination layout="prev, pager, next" total={50}/>
            </div>

            <ProductDialog visible={dialogVisible} setVisible={setDialogVisible}/>
        </div>
    )
}

export default ProductCards
