import request from './https'
import {AxiosPromise} from 'axios'
import {AmazonsChoice, Sponsored} from "../types/seo";

export function getSponsored() {
    return request({
        url:'/SEO/sponsored',
        method: 'GET'
    }) as AxiosPromise<Sponsored>
}

export function getAmazonsChoice() {
    return request({
        url:'/SEO/amazons_choice',
        method: 'GET'
    }) as AxiosPromise<AmazonsChoice>
}
