import React, {useEffect, useRef, useState} from 'react'
import * as echarts from "echarts/core";
import {Button} from "element-react";
import {HiDownload} from "react-icons/hi";
import {downloadPng} from "../../../utils/download";
import {LineChart, LineSeriesOption} from "echarts/charts";
import {
    GridComponent,
    GridComponentOption,
    TooltipComponent,
    TooltipComponentOption
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";

type ECOption = echarts.ComposeOption<LineSeriesOption | GridComponentOption | TooltipComponentOption>;

echarts.use([LineChart, GridComponent, TooltipComponent, CanvasRenderer])

function Keyword() {
    const chartRef = useRef<HTMLDivElement | null>(null);
    const [selectValue, setSelectValue] = useState(1); // Unit: Month

    const option: ECOption = {
        grid: {
            top: '10%',
            right: '5%',
            bottom: '10%',
            left: '15%',
        },
        xAxis: [
            {
                type: 'category',
                data: ['', '', '', '', '', '', '', '', '']
            }
        ],
        yAxis: [
            {
                type: 'value',
                inverse: true,
                position: 'top',
                min: 1,
            }
        ],
        series: [
            {
                type: 'line',
                name: 'Rank',
                data: [10, 2, 1, 3, 4, 6, 5, 7, 8],
            },
        ]
    }

    useEffect(() => {
        if (!chartRef.current) return

        const chart = echarts.init(chartRef.current, 'customed')
        chart.setOption(option)
    })

    const rangeChangeHandler = (value: number) => {
        setSelectValue(value)
    }

    const onDownload = async () => {
        if (!chartRef.current) return

        await downloadPng(chartRef.current, 'title')
    }

    return (
        <div className="relative border-2 rounded mx-8 mt-10" style={{height: '400px'}}>
            <HiDownload className="absolute right-2 top-2 text-3xl cursor-pointer" onClick={onDownload}/>
            <div className="flex items-center px-2" style={{height: '50px'}}>
                <h1 className="text-xl mr-4">Keyword in Past</h1>
                <div className="flex">
                    <Button type={selectValue === 1 ? 'primary' : undefined}
                            size="mini"
                            onClick={() => rangeChangeHandler(1)}
                    >1 Month</Button>
                    <Button type={selectValue === 3 ? 'primary' : undefined}
                            size="mini"
                            onClick={() => rangeChangeHandler(3)}
                    >3 Months</Button>
                    <Button type={selectValue === 6 ? 'primary' : undefined}
                            size="mini"
                            onClick={() => rangeChangeHandler(6)}
                    >6 Months</Button>
                    <Button type={selectValue === 12 ? 'primary' : undefined}
                            size="mini"
                            onClick={() => rangeChangeHandler(12)}
                    >1 Year</Button>
                </div>
            </div>
            <div ref={chartRef} style={{height: '250px', width: '570px'}}/>
            {/*Keywords*/}
            <div style={{height: '100px'}}>
                <h2 className="text-base text-context text-center mt-1">Top 10 Keywords</h2>
                <div className="border-2 rounded p-2 w-5/6 mx-auto flex" style={{height: '60px'}}>
                    <p>Webcam,</p>
                    <p>Webcam,</p>
                    <p>Webcam,</p>
                    <p>Webcam,</p>
                    <p>Webcam,</p>
                    <p>Webcam</p>
                </div>
            </div>
        </div>
    )
}

export default Keyword
