import React, {useState} from 'react'
import {Card, Pagination, Select} from "element-react";

import BarLineChart from "../components/BarLineChart";
import DoughnutChart from "../components/DoughnutChart";
import PercentageDoughnutChart from "../components/PercentageDoughnutChart";
import HorizontalBarChart from "../components/HorizontalBarChart";
import InfoCard from '../components/InfoCard'
import ProductCards from "../components/ProductCards";
import VerticalProgressBarChart from "../components/VerticalProgressBarChart";

function initBrandOptions(): ElementReact.SelectOptionProps[] {
    return [
        {label: 'Guangzhou Shirui', value: 'guangzhou_shirui'},
        {label: 'A', value: 'a'},
        {label: 'B', value: 'b'}
    ]
}

function initCodeOptions(): ElementReact.SelectOptionProps[] {
    return [
        {label: 'All', value: 'all'},
        {label: 'A', value: 'a'},
        {label: 'B', value: 'b'}
    ]
}

function AmazonTop100DashBoard() {
    const [brand, setBrand] = useState('guangzhou_shirui')
    const [code, setCode] = useState('all')

    const brandOptions = initBrandOptions()
    const codeOptions = initCodeOptions()

    return (
        <div>
            {/* Search Bar */}
            <div className="flex items-end mb-4">
                <Select className="mr-4" value={brand} onChange={value => setBrand(value)}>
                    {
                        brandOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <Select className="mr-4" value={code} onChange={value => setCode(value)}>
                    {
                        codeOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>

                <p className="text-xl text-context">Last Update: 2021/07/31</p>
            </div>

            {/* Dash Board */}
            <div className="flex">
                <div className="w-7/12 pr-2">
                    <Card className="mb-4">
                        <BarLineChart title="Price Distribution" height="400px" data={null}/>
                    </Card>
                    <div className="flex mb-4">
                        <div className="w-1/2 pr-2">
                            <InfoCard title="Most Popular Resolution" context="1080P" reviewsContext="2,000"/>
                        </div>
                        <div className="w-1/2 pl-2">
                            <InfoCard title="Most Popular Price Range" context="$50~60" reviewsContext="1,200"/>
                        </div>
                    </div>
                    <Card>
                        <HorizontalBarChart title="Top Product" height="400px" data={[[], []]}/>
                    </Card>
                </div>
                <div className="w-5/12 pl-2">
                    <Card className="mb-4">
                        <DoughnutChart title="Top Brand" height="300px"/>
                    </Card>
                    <div className="flex mb-4">
                        <div className="w-1/2 pr-2">
                            <Card>
                                <PercentageDoughnutChart
                                    height="200px"
                                    title="Sponsored Rate"
                                    percentage={60}
                                />
                            </Card>
                        </div>
                        <div className="w-1/2 pl-2">
                            <Card>
                                <PercentageDoughnutChart
                                    height="200px"
                                    title="Amazon's Choice"
                                    percentage={70}
                                />
                            </Card>
                        </div>
                    </div>
                    <Card>
                        <VerticalProgressBarChart title="Top Product" height="400px" data={null}/>
                    </Card>
                </div>
            </div>

            {/* Product Cards */}
            <div className="mt-16">
                <div className="flex justify-between items-end">
                    <p className="text-2xl">Recent Top 100</p>
                    <Select value={code}>
                        {
                            codeOptions.map(it => {
                                return <Select.Option key={it.value} label={it.label} value={it.value}/>
                            })
                        }
                    </Select>
                </div>

                <ProductCards/>
            </div>
        </div>
    )
}

export default AmazonTop100DashBoard
