import React, {useState} from 'react'
import {Button, Card, DateRangePicker, Select} from "element-react";
import {FaListAlt} from 'react-icons/fa'
import {RiBarChartBoxFill} from 'react-icons/ri'

import ProductCards from "../components/ProductCards";
import {init30DaysRangeDateType, initCategoryOptions, initRegionOptions} from "../utils/mock";
import HorizontalBarChart from "../components/HorizontalBarChart";
import WordCloudChart from '../components/WordCloudChart';

enum CHART_TYPE {
    CHART,
    TABLE
}

function KeywordAnalysis() {
    const [brand, setBrand] = useState('guangzhou_shirui')
    const [code, setCode] = useState('all')
    const [date, setDate] = useState<ElementReactLibs.dateType[] | undefined>(init30DaysRangeDateType())
    const [chartType, setChartType] = useState(CHART_TYPE.CHART)

    const brandOptions = initCategoryOptions()
    const codeOptions = initRegionOptions()

    const onChartTypeChange = () => {
        if (chartType === CHART_TYPE.CHART) setChartType(CHART_TYPE.TABLE)
        if (chartType === CHART_TYPE.TABLE) setChartType(CHART_TYPE.CHART)
    }

    return (
        <div>
            {/* Search Bar */}
            <div className="flex items-center mb-4">
                <Select className="mr-4" value={brand}>
                    {
                        brandOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <Select className="mr-4" value={code}>
                    {
                        codeOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <DateRangePicker
                    className="mr-4"
                    value={date}
                    onChange={date => {
                        console.debug('DateRangePicker1 changed: ', date)
                        setDate(date)
                    }}
                />
                <div className="text-2xl cursor-pointer mr-4" onClick={onChartTypeChange}>
                    {
                        chartType === CHART_TYPE.CHART && <FaListAlt/>
                    }
                    {
                        chartType === CHART_TYPE.TABLE && <RiBarChartBoxFill/>
                    }
                </div>
                <Button type="primary">Download Products Distribution XLSL</Button>
            </div>

            <div className="flex">
                <div className="w-2/3 pr-2">
                    <Card className="mb-4">
                        {
                            chartType === CHART_TYPE.CHART && <WordCloudChart height="600px" />
                        }
                        {
                            chartType === CHART_TYPE.TABLE && <div/>
                        }
                    </Card>
                </div>

                <div className="w-1/3 pl-2">
                    <Card>
                        <HorizontalBarChart title="Search Volume" height="600px" data={[[],[]]}/>
                    </Card>
                </div>
            </div>

            {/* Product Cards */}
            <div className="mt-16">
                <div className="flex justify-between items-end">
                    <p className="text-2xl">Webcam(change)</p>
                    <Select value={code}>
                        {
                            codeOptions.map(it => {
                                return <Select.Option key={it.value} label={it.label} value={it.value}/>
                            })
                        }
                    </Select>
                </div>

                <ProductCards/>
            </div>
        </div>
    )
}

export default KeywordAnalysis
