import dayjs from "dayjs";

export function initCategoryOptions(): ElementReact.SelectOptionProps[] {
    return [
        {label: 'Webcam', value: 'webcam'},
        {label: 'Video projector', value: 'video projector'},
        {label: 'Document camera', value: 'document camera'},
    ]
}

export function initRegionOptions(): ElementReact.SelectOptionProps[] {
    return [
        {label: 'US', value: 'US'},
        {label: 'UK', value: 'UK'},
    ]
}

export function init30DaysRangeDateType(): ElementReactLibs.dateType[] {
    const today = dayjs();
    return [today.subtract(1, 'month').toDate(), today.toDate()];
}
