import React, {useEffect, useState} from 'react'
import {Card, DateRangePicker, Pagination, Select, Tooltip} from "element-react";
import {AiFillInfoCircle} from 'react-icons/ai'
import {FaListAlt} from 'react-icons/fa'
import {RiBarChartBoxFill} from 'react-icons/ri'

import BarLineChart from "../components/BarLineChart";
import ProductCards from "../components/ProductCards";
import Collapse from "../components/Collapse";
import {init30DaysRangeDateType, initCategoryOptions, initRegionOptions} from "../utils/mock";
import HorizontalBarChart from "../components/HorizontalBarChart";
import {getNewCommentBarChart} from "../apis/market";
import {NewCommentBarChart, NewCommentBarItem, ProductBarChart, ProductBarItem} from "../types/market";

enum CHART_TYPE {
    CHART,
    TABLE
}

function formatData(data: NewCommentBarChart): [string[], number[]] {
    const formatContent = data.items.content.sort((a: NewCommentBarItem, b: NewCommentBarItem) => b.gap - a.gap)
    const yAxisData = formatContent.map((it: NewCommentBarItem) => it.asin)
    const seriesData = formatContent.map((it: NewCommentBarItem) => it.gap)
    return [yAxisData, seriesData]
}

function NewReviews() {
    const [category, setCategory] = useState('webcam')
    const [region, setRegion] = useState('US')
    const [mission, setMission] = useState('2021-09-19')
    const [date, setDate] = useState<ElementReactLibs.dateType[] | undefined>(init30DaysRangeDateType())
    const [chartType, setChartType] = useState(CHART_TYPE.CHART)
    const [page, setPage] = useState(1)
    const [newComment, setNewComment] = useState<NewCommentBarChart | null>(null)

    const categoryOptions = initCategoryOptions()
    const regionOptions = initRegionOptions()

    useEffect(() => {
        setPage(1)
    }, [category, region, mission])

    useEffect(() => {
        const fetchData = async () => {
            const {data} = await getNewCommentBarChart({category, region, mission, page})
            setNewComment(data)
        }
        fetchData()
    }, [page])

    const onChartTypeChange = () => {
        if (chartType === CHART_TYPE.CHART) setChartType(CHART_TYPE.TABLE)
        if (chartType === CHART_TYPE.TABLE) setChartType(CHART_TYPE.CHART)
    }

    return (
        <div>
            {/* Search Bar */}
            <div className="flex items-center mb-4">
                <Select className="mr-4" value={category} placeholder="Select" onChange={(value: string) => setCategory(value)}>
                    {
                        categoryOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <Select className="mr-4" value={region} placeholder="Select" onChange={(value: string) => setRegion(value)}>
                    {
                        regionOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <DateRangePicker
                    className="mr-4"
                    value={date}
                    onChange={date => {
                        console.debug('DateRangePicker1 changed: ', date)
                        setDate(date)
                    }}
                />
                <Tooltip
                    className="mr-4"
                    effect="dark"
                    content={
                        <div style={{maxWidth: '200px'}}>
                            Sold Amount is estimated by multiplaying new reviews in each price range by 100.
                        </div>
                    }
                    placement="bottom"
                >
                    <AiFillInfoCircle className="text-2xl"/>
                </Tooltip>
                <div className="text-2xl cursor-pointer" onClick={onChartTypeChange}>
                    {
                        chartType === CHART_TYPE.CHART && <FaListAlt/>
                    }
                    {
                        chartType === CHART_TYPE.TABLE && <RiBarChartBoxFill/>
                    }
                </div>
            </div>

            <div className="flex">
                <div className="w-2/3 pr-2">
                    <Card className="mb-4">
                        {
                            chartType === CHART_TYPE.CHART && newComment &&
                            <div>
                              <HorizontalBarChart title="Top Product" height="572px" data={formatData(newComment)}/>
                              <div className="flex justify-end">
                                <Pagination
                                  layout="prev, pager, next"
                                  pageCount={newComment.info.max_page}
                                  small={true}
                                  currentPage={newComment.items.current_page}
                                  onCurrentChange={(value: number) => setPage(value)}
                                />
                              </div>
                            </div>
                        }
                        {
                            chartType === CHART_TYPE.TABLE &&
                            <BarLineChart title="Price Distribution" height="600px" data={null}/>
                        }
                    </Card>
                </div>

                <div className="w-1/3 pl-2">
                    <Card>
                        <Collapse height="600px"/>
                    </Card>
                </div>
            </div>

            {/* Product Cards */}
            <div className="mt-16">
                <div className="flex justify-between items-end">
                    <p className="text-2xl">Group by Product(change)</p>
                    <p className="text-2xl">Ranked by New Review</p>
                </div>

                <ProductCards/>
            </div>
        </div>
    )
}

export default NewReviews
